import * as React from 'react';

import { Toggle } from '.';

export default {
  title: 'website/Toggle',
  component: Toggle,
  argTypes: {
    backgroundColor: { control: 'color' },
  },
};

export const Base = () => {
  const [mode, setMode] = useState(false);

  const onModeChange = (checked) => {
    setMode(checked);
  }

  return (
    <Toggle id="display_mode" name="display_mode" checked={mode} onChange={ onModeChange } />
  )
};


